import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from 'ngx-alerts';
import { ArtiService } from '../servisi/arti.service';
@Component({
  selector: 'app-adding',
  templateUrl: './adding.component.html',
  styleUrls: ['./adding.component.css']
})
export class AddingComponent implements OnInit {
  imeArtikla: String;
  sifra: String;
  cena: String;
  opis: String;
  fileArt: File = null;
  // public slika="http://localhost:3000/artikli/image/";
  im:String='';
  akcija:Boolean;
  
  constructor(
    private router: Router,
    private alertService: AlertService,
    private artiService: ArtiService
  ) { }

  ngOnInit() {
    window.scrollTo(0,0);
  }
  onImageAdded(event) {
   
    this.fileArt = event.target.files[0];
    this.im=this.fileArt.name;
  }
  
 
  onArikalSubmit() {
    if (this.fileArt != null) {
      const artikal = {
        imeArtikla: this.imeArtikla,
        sifra: this.sifra,
        cena: this.cena,
        opis: this.opis,
        akcija:this.akcija.valueOf()
        
      }
      this.artiService.addArtikal(artikal, this.fileArt).subscribe(data => {
        if (data.success) {
          this.alertService.success('Uspesno dodato ');
          this.imeArtikla=null;
          this.sifra=null;
          this.cena=null;
          this.opis=null;
          this.im=null;
          window.scrollTo(0,0);
         // this.router.navigate(['']);
        }
      })
    }
  }

}
