import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';

import { map } from 'rxjs/operators';

import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class ArtiService {
  artikal: any;
  fileArt: File;

  constructor(private http: Http, private authService: AuthService) { }

  addArtikal(artikal, fileArt) {

    var body = JSON.stringify(artikal);
    const formData: FormData = new FormData();
    formData.append('thisArti', body);
    if (fileArt != null) formData.append('file', fileArt, fileArt.name);
    let headers = new Headers();
    console.log(body);

    return this.http.post('http://localhost:3000/artikli/add', formData, { headers: headers }).pipe(map(res => res.json()));

  }

  getAllArtikle() {

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/artikli/all', { headers: headers }).pipe(map(res => res.json()));

  }
  brisi(ima:String){

    let headers = new Headers();
    this.authService.loadToken();

    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.authService.authToken);
    return this.http.delete('http://localhost:3000/artikli/brisi/' + ima, { headers: headers }).pipe(map(res => res.json()));

  }
  deleteArtikal(artId: String ) {


    let headers = new Headers();
    this.authService.loadToken();

    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.authService.authToken);
    
    return this.http.delete('http://localhost:3000/artikli/delete/' + artId, { headers: headers }).pipe(map(res => res.json()));



  }



  /*
  getImage(art:string){
    
    let headers=new Headers();
    //headers.append('Content-Type','application/json');
    return  this.http.get('http://localhost:3000/artikli/image/'+art, { headers: headers }).pipe(map(res => res.json()));
 
 
  }*/


}
