import { Component, OnInit } from '@angular/core';
import { AuthService } from '../servisi/auth.service';
import { Router } from '@angular/router';
import { AlertService } from 'ngx-alerts';
import { ValidateService } from '../servisi/validate.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: String=null;
  password: String=null;


  constructor(
    private authService: AuthService,
    private router: Router,
    private alertService: AlertService,
    private validateService: ValidateService
  ) { }

  ngOnInit() {
    window.scrollTo(0,0);
  }
  onLoginSubmit() {

    var user = {
      username: this.username,
      password: this.password
    }

    if (this.password != null && this.username != null) {
      if (!this.validateService.validateEmail(user.username)) {
        this.alertService.danger('Niste uneli validan email ili password');
        return false;
      } else {
        
        this.authService.authenicateUser(user).subscribe(data => {
          if (data.success) {
            this.authService.storeUserData(data.token, data.user);
            this.alertService.success('Uspesno ste ulogovani');
            this.router.navigate(['']);

          } else {
            this.alertService.danger('Niste uneli validan email ili password');
            this.password=null;
           
            this.router.navigate(['login']);
          }
        });
      }
    } else {
      this.alertService.warning('Niste popunili sva polja!');

    }

  }


}
