import { Component, OnInit, Input } from '@angular/core';
import {ImageeService}  from './imser/imagee.service';


@Component({
  selector: 'app-galerija',
  templateUrl: './galerija.component.html',
  styleUrls: ['./galerija.component.css']
})
export class GalerijaComponent implements OnInit {
  images:any[];
  filterBy?:string='all'
  visibleImages:any[]=[];
   


  constructor(private imageeService:ImageeService) {
    this.visibleImages=this.imageeService.getImages();
   }


  ngOnInit() {
    window.scrollTo(0,0);
  }
  ngOnchanges(){
    this.visibleImages=this.imageeService.getImages();

  }
  idiNa(){
    window.scrollTo(0,50);
  }
}
