import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ImageeService {
  visibleImages=[];
  constructor() { }
  getImages(){
    return this.visibleImages=IMAGES.slice(0);
  }
  getImage(id:number){
    return IMAGES.slice(0).find(image =>image.id==id);
  }


}

