import { Component, OnInit } from '@angular/core';
import { ImageeService} from '../imser/imagee.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-image-detalis',
  templateUrl: './image-detalis.component.html',
  styleUrls: ['./image-detalis.component.css']
})
export class ImageDetalisComponent implements OnInit {
  image:any;
  constructor(private imageeService:ImageeService, private route:ActivatedRoute) { }

  ngOnInit() {
    this.image=this.imageeService.getImage(
      +this.route.snapshot.params['id']
    );
  }

}
