import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from 'ngx-alerts';
import { PorudzService } from '../servisi/porudz.service';
import { ValidateService } from '../servisi/validate.service';
@Component({
  selector: 'app-porudzbina',
  templateUrl: './porudzbina.component.html',
  styleUrls: ['./porudzbina.component.css']
})

export class PorudzbinaComponent implements OnInit {

  imeIprezime: String;
  email: String;
  opis: String;
  kontakt: String;
  adresa: String;
  //latitude=44.272153;
  //longitude=19.882253;

  constructor(private router: Router, private alertService: AlertService, private porudzService: PorudzService, private validateService: ValidateService) { }

  ngOnInit() {
    window.scrollTo(0, 0);

  }

  onPorInfoSubmit() {

    const porudz = {

      imeIprezime: this.imeIprezime,
      adresa: this.adresa,
      kontakt: this.kontakt,
      email: this.email,
      opis: this.opis
    }


    if (this.imeIprezime != null&&this.kontakt!=null &&this.opis!=null) {
     

      this.porudzService.porudzbina(porudz).subscribe(data => {
        if (data.success) {
          
        } else {
          this.alertService.danger('Doslo je do greske');
          return false;
        }
      });
      this.alertService.success('Uspesno ste poslali poruku');
      this.imeIprezime = null;
      this.adresa = null;
      this.kontakt = null;
      this.email = null;
      this.opis = null;
      window.scrollTo(0, 0);
      

    }
    else{
      this.alertService.danger('Niste popunili odgovarajuca polja');

    }


   


  }


}
