import { Component, OnInit } from '@angular/core';
import { AlertService } from 'ngx-alerts';
import { ArtiService } from '../servisi/arti.service';
import { AuthService } from '../servisi/auth.service';
import { BROWSER_SANITIZATION_PROVIDERS } from '@angular/platform-browser/src/browser';
import { timeInterval } from 'rxjs/operators';



@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  //items: Array<any> = [];
  artikal: Object[];
  pik: File = null;
  public slika = "http://localhost:3000/artikli/image/";
 
  numb: any;
  ima: any;
  constructor(private artiService: ArtiService, private authService: AuthService, private alertService: AlertService) {
    /*
        this.items=[
         
          {name:'../../assets/homePic/6.jpg'},
          {name:'../../assets/homePic/1.jpg'},
          {name:'../../assets/homePic/slikaRadnje.jpg'},
          {name:'../../assets/homePic/lusteri.jpg'},
          {name:'../../assets/homePic/2.jpg'}
    
        ];*/
  }

  ngOnInit() {

    this.artiService.getAllArtikle().subscribe(data => {
      this.artikal = data.obj;
      this.artikal.reverse();
     
    });
   
    window.scrollTo(0, 0);
  }
  
  izbrisiArtikal(art) {

    if (this.authService.isLogged) {
      var ar = art._id;
      this.ima = art.fileArt;
      

      this.artiService.brisi(this.ima).subscribe(data => {
        if (data.success) {
          if (this.artiService.deleteArtikal(ar).subscribe()) {

            this.alertService.success('Uspesno ste obrisali');
            window.location.reload();

          } else {
            this.alertService.info('Doslo je do greske u brisanju');
          }
        }else {
          this.alertService.info('Doslo je do greske u brisanju');
        }
      });
    } else {
      console.log('Niste ulogovani');
    }
  }

  idiNa() {
    window.scrollTo(0, 200);
  }
  isLoggedIn() {
    //ako je prijavljen
    if (this.authService.isLogged()) {
      return true;
    }
  }
  getImageForArt(art) {


    var filename = art.fileArt;
    return this.slika + filename;
    //return this.artiService.getImage(filename);


  }

}
